import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import home from '@/pages/home'
Vue.use(Router);
const routes = [
    {
        path: '/',
        name: 'HelloWorld',
        component: HelloWorld,
    },
    {
        path: '/home',
        name: 'home',
        component: home
    },
    {
        path: '/home/discover',
        name: 'discover',
        component: home
    }
]
const router = new Router({
    routes
});
export default router



